<template>
    <div class="header">
        <div class="header-left">
            <span class="iconfont icon-left"></span>
        </div>
        <div class="header-input">
            <span class="iconfont icon-search"></span>
            输入城市/景点/游玩/主题
        </div>
        <router-link to="/city">
            <div class="header-right">
                {{this.city}}
                <span class="iconfont icon-down iconDown"></span>
            </div>
        </router-link>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped>
    @import '~styles/varibles.styl'
    .header
        display: flex
        line-height: 86px
        height: 86px
        background: $bgColor
        color: #fff
        align-items: center
        .header-left
            margin: 0 10px 0 5px
            width: 64px
        .header-input
            flex: 1
            background: #fff
            border-radius: .1rem
            height: 64px
            line-height: 64px
            color: lightgrey
            padding-left: 22px
        .header-right
            min-width: 124px
            padding: 0 10px
            text-align: center
            color: #fff
            .iconDown
                font-size: 22px
</style>



